export interface OnAuthSuccess {
  (): Promise<void>;
}

export interface BootstrapperAuthClient {
  ensureAuthed(): Promise<{}>
}

export interface EnsureAuthedError extends Error {
  message: string;
  error: string
}

export type OnAuthFail =
  | (() => Promise<{ handleAuthError: (error: Error) => void }>)
  | ((error: EnsureAuthedError) => void);

export interface AsyncAppBootStrapper {
  (authClient: BootstrapperAuthClient,
    authSuccess: OnAuthSuccess,
    authFail?: OnAuthFail): Promise<void>;
}

export const bootStrapAppAsync: AsyncAppBootStrapper = async (
  authClient: BootstrapperAuthClient,
  happyPath: OnAuthSuccess,
  sadPath?: OnAuthFail): Promise<void> => {

  let authError: EnsureAuthedError | null = null;
  try {
    await authClient.ensureAuthed();
  } catch (e) {
    authError = e as EnsureAuthedError;
  }

  if (authError) {
    if (sadPath) {
      sadPath(authError);
    } else {
      const { handleAuthError } = await import('./handleAuthError');
      handleAuthError(authError);
    }
  } else {
    try {
      await happyPath();
    } catch (e) {
      (document.getElementById("root") as HTMLElement).innerHTML = `<h2>An error occurred while starting the application.</h2><p>${e}</p>`;
    }
  }
};